:root {
  --colour-white: #fff;
  --colour-black: #000;
  --colour-brown: hsl(25, 45%, 47%);
  --colour-light-yellow: hsl(44, 24%, 91%);
  --colour-dark-yellow: hsl(43, 16%, 69%);
  --colour-darker-yellow: hsl(43, 16%, 55%);
  --colour-darkest-yellow: hsl(43, 16%, 35%);
  --colour-nav-links: hsl(26, 47%, 20%);
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body,
button,
span {
  font-family: "Poppins", sans-serif;
  font-weight: 400;
}

a {
  text-decoration: none;
  color: inherit;
}

body {
  font-size: 20px;
}

.bold {
  font-weight: 700;
}

.semi-bold {
  font-weight: 600;
}

.logo {
  font-family: "Oregano", cursive;
  font-size: 45px;
  color: hsl(25, 45%, 47%);
  color: var(--colour-brown);
}

.primary-btn {
  background-color: hsl(25, 45%, 47%);
  background-color: var(--colour-brown);
  color: #fff;
  color: var(--colour-white);
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.25);
  border: 0;
  border-radius: 10px;
  cursor: pointer;
  font-size: inherit;
}

.main-btn-size {
  width: 260px;
  height: 58px;
}

.flex-column-center {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

ul {
  list-style: none;
}

